body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('./assets/young-man-sportswear-exercise-class-gym.jpg');
  background-size:cover;                    
  background-repeat:   no-repeat;
  background-attachment: fixed;
  background-position: center center;
}

#header{
  color: #fff;
  background-color: rgba(50, 50, 50);
  padding: 30px 30px 5px 30px;
  font-weight: 300;
  font-size: 32px;
  font-style: italic;
  margin: 0;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.headernavbar{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgba(40, 40, 40);
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}

.headernavbar li {
  float: left;
}

.headernavbar a{
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.headernavbar a:hover{
  background-color: #111;
  cursor: pointer;
}

/*SINGLEWORKOUT*/

.singleWorkout{
  color: azure;
}

.singleWorkout article{
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  border: thick;
  border-color: rgba(0, 0, 0, 0.60);
  border-style: solid;
  border-width: 2px;
  background-color: rgba(50, 50, 50, 0.85);
  border-radius: 20px;
  display: block;
  width: 500px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}

.themeDate{
  display: flex;
  justify-content: space-around;
}

.themeDate h2{
  font-weight: 200;
  font-size: 24px;
}

#movementsHead{
  margin: 0;
  display: flex;
  justify-content: center;
  font-weight: 200;
  font-size: 22px;
  text-decoration: underline;
  text-underline-offset: 0.1em;
}

.movementsUl{
  list-style: none;
}

#movementName{
  font-size: 18px;
  font-weight: 200;
  
}

#movementSet{
  font-size: 18px;
  font-weight: 200;
}

.weightsUl{
  list-style: none;
  display: inline-block;
  font-weight: 200;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.60);
  border-width: 3px;
  border-radius: 2px;
  padding: 2px 10px 2px 10px;
}

.weightsUl li{
  text-align: center;
}

#cardioHead{
  margin: 0;
  display: flex;
  justify-content: center;
  font-weight: 200;
  font-size: 22px;
  text-decoration: underline;
  text-underline-offset: 0.1em;
}

.cardioUl{
  list-style: none;
  font-size: 18px;
  font-weight: 200;
}

/*SINGLEWORKOUT*/

/*HISTORY*/

.historySection{
  color: azure;
}

.historySection h2{
  font-weight: 200;
  font-size: 24px;
  text-align: center;
}

.historySection article{
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  border: thick;
  border-color: rgba(0, 0, 0, 0.60);
  border-style: solid;
  border-width: 2px;
  background-color: rgba(50, 50, 50, 0.85);
  border-radius: 20px;
  width: 500px;
  max-width: 500px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  height: 600px;
  max-height: 100%;
}

.historySection nav {
  height: auto;
  max-height: 500px;
  overflow-y: scroll;
  scrollbar-color: rebeccapurple green;
  scrollbar-width: thin;
}

.historySection ul{
  list-style-type: none;
  text-align: center;
  margin: 0 auto;
  padding: 0px;
  border:1px solid rgba(0, 0, 0, 0.60);
  overflow: hidden;
}

.historySection li{
  float: left;
  padding: 2px;
  width: 150px;
  margin-left: auto ;
  margin-right: auto ;
}

.linktoworkout{
  color: azure;
  text-decoration: none;
}

.linktoworkout:hover{
  color: #111;
  transition-duration: 0.2s;
}

/*HISTORY*/


/*ADDDDD*/
.addWorkout{
  color: azure;
  display: grid;
  grid-template-columns: auto auto;
  padding: 10px;
}
.addWorkout article{
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  border: thick;
  border-color: rgba(0, 0, 0, 0.60);
  border-style: solid;
  border-width: 2px;
  background-color: rgba(50, 50, 50, 0.85);
  border-radius: 20px;
  display: block;
  width: 500px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}

/*THEMEDATEDURATION*/

.infoadd{
  text-align: center;
}
.infoadd h2{
  text-align: center;
  font-weight: 200;
  font-size: 24px;
}
.infoadd ul{
  list-style: none;
  
  margin: 0;
  padding: 0;
}
.infoadd li{
  font-weight: 200;
  font-size: 18px;
  margin: 2px;
}
.infoadd input{
  padding: 3px;
  background-color: #111;
  color: azure;
  font-weight: 200;
  margin: 2px;
}
.infoadd select{
  font-weight: 200;
  margin: 2px;
  padding: 3px;
  width: 180px;
  text-align: center;
  background-color: #111;
  border-style: none;
  color: azure;
}
.infoadd select:hover{
  cursor: pointer;
}
.inputtime {
  margin-left: auto;
  margin-right: auto;
  width: 100px;
  border: none;
  color: azure;
  text-align: center;
  
}
.inputtime:hover{
  cursor: pointer;
}
.datepicker{
  padding: 4px !important;
  text-align: center;
  border-style: none;
}

/*THEMEDATEDURATION*/

/*MOVEMENT*/

.movementadd{
  margin-left: auto;
  margin-right: auto;
  border: 1px solid rgba(0, 0, 0, 0.60);
  width: 350px;
}

.movementhead{
  text-align: center;
  font-weight: 200;
  font-size: 24px;
}
.movementadd div {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.movementadd p{
  margin: 0;
  background-color:#222;
  padding: 2px;
  font-weight: 200;
  font-size: 18px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.60);
  border-right: 1px solid ;
  text-align: center;
}
.movementadd input {
  border-style: none;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.60);
  padding: 2px;
  background-color: #111;
  color: #fff;
}
.savemovementbutton{
  display: grid;
  grid-template-columns: auto !important;
}
.savemovementbutton button{
  background-color: azure;
  border: none;
  padding: 5px;
  color: #111;
  font-weight: 200;
  font-size: 12px;
}
.savemovementbutton button:hover{
  cursor: pointer;
  background-color:rgba(255, 255, 255, 0.6);
  transition-duration: 0.2s;
}

/*MOVEMENT*/
